import React from 'react';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import { Typography, Link } from '@material-ui/core';
import { makeStyles  } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import James from './ca_james.jpg'
import Venkatakrishnan from './ca_venkat.jpg'
import Murali from './ca_murali.jpg'
import Padmanabha from './ca_padmanabha_pillai.jpg'
import Alex from './ca_alex.jpg'
import Sreenath from './ca_sreenath.jpg'
import Sasidharan from './user-icon.png'
import Saleem from './ca_saleem.jpg'
import Krishna from './ca_krishna_raghupathy.jpg'
import Abraham from './ca_abraham_thomas.jpg'
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';

const useStyles = makeStyles ({
    root: {
        maxWidth: 345
    },
    rootTyp: {
        paddingTop: '10px'
    }
});

function AboutUs() {
    const classes = useStyles();
    return (
        <Container maxWidth="lg" style={{'paddingBottom': '10px'}}>
            <Grid container direction="row">
                <Grid item xs={12}>
                    <Typography variant="h5" classes={{root: classes.rootTyp}}>
                        {"About Us"}
                    </Typography>
                    <Typography variant="body1" classes={{root: classes.rootTyp}}>
                        {"A P J M V & ASSOCIATES LLP is a Limited Liability Partnership (LLP) firm. It is registered with Registrar of Companies, Ernakulam on Sep 21, 2017 by 5 chartered accountants with rich experience in accounting, management and consulting fields. Main business activities of A P J M V & Associates LLP are Audit, Consultancy, Management Services and associated activities. The LLP has 8 working partners and 2 designated partners. The designated partners are James Menachery and Venkatakrishnan Sivasailam."}
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Link href="https://www.linkedin.com/company/a-p-j-m-v-associates-llp" target="_blank">
                        <LinkedInIcon fontSize="medium" style={{fill: '#0e76a8'}}/>
                    </Link>
                    &nbsp;&nbsp;
                    <Link href="#" target="_blank">
                        <TwitterIcon fontSize="medium" style={{fill: '#1976d2'}}/>
                    </Link>
                    &nbsp;&nbsp;
                    <Link href="#" target="_blank">
                        <FacebookIcon fontSize="medium" style={{fill: 'rgb(17, 82, 147)'}}/>
                    </Link>
                </Grid>
                <Grid item xs={12}>
                    <Typography variant="h6" classes={{root: classes.rootTyp}}>
                        {"Our Team"}
                    </Typography>
                </Grid>
            </Grid>
            <Grid container direction="row" spacing={4} style={{'paddingTop': '10px'}}>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="James"
                            image={James}
                            title="James"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. James Menachery"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Founder, Managing Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Venkatakrishnan"
                            image={Venkatakrishnan}
                            title="Venkatakrishnan"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Venkatakrishnan S"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Founder, Designated Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Murali"
                            image={Murali}
                            title="Murali"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Murali Nair V"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Founder, Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Padmanabha"
                            image={Padmanabha}
                            title="Padmanabha"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Padmanabha Pillai S"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Founder, Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Alex"
                            image={Alex}
                            title="Alex"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Alex Jose"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Founder, Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
            </Grid>
            <Grid container direction="row" spacing={4}>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Sreenath"
                            image={Sreenath}
                            title="Sreenath"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Sreenath V Kamath"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Saleem"
                            image={Saleem}
                            title="Saleem"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. P V Saleem"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Sasidharan"
                            image={Sasidharan}
                            title="Sasidharan"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. P B Sasidharan Pillai"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Krishna"
                            image={Krishna}
                            title="Krishna"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p" style={{fontSize: '0.765rem'}}>
                                {"CA. Krishna Kumar Raghupathy"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Abraham"
                            image={Abraham}
                            title="Abraham"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Abraham Thomas"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Partner
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    );
}

export default AboutUs;